import React, { Component } from 'react'
import { withPrefix } from 'gatsby'

export default class ProjectPreview extends Component {
  render() {
    return (
      <div className="card project-preview m-1 animated fadeInRight" style={{ width: '250px', }}>
            <div className="view zoom">
              <img className="card-img-top img-fluid" src={withPrefix( `img/${this.props.image}` )} alt="project-img"/>
              <div className="mask flex-center"></div>
            </div>

              <div className="card-body">
                <h6 className="card-title border-bottom border-light p-2">{this.props.name}</h6>
                <p className="card-text text-center">{this.props.startyear} - {this.props.endyear}<br />
                {this.props.services}.<br />
                www.{this.props.link}</p>
              </div>
      </div>
    );
  }
}